.sidebar {
  height: 100dvh;
  background-color: black;
  position: absolute;
  top: 0;
  z-index: 20;
  left: 0;
  background: rgb(2, 0, 36);
  background: linear-gradient(90deg, #090a0f 10%, #1b2735 100%);
}

.sidebar-open {
  width: 80vw;
}
.sidebar-closed {
  width: 0;
}

@media (min-width: 1024px) {
  .sidebar {
    display: none;
  }
  .hamburger-react {
    display: none;
  }
}

// Hamburger
.hamburger {
  position: absolute;
  z-index: 21;
  top: 0;
  left: 0;
}
.hamburger-open {
  right: 0;
  left: 69vw;
}
