.blob {
  border-radius: 50%;
  box-shadow: 0 0 0 0 rgba(52, 172, 224, 1);
  transform: scale(1);
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% {
    transform: scale(0.98);
    box-shadow: 0 0 0 0 rgba(52, 172, 224, 0.7);
  }
  
  50% {
    transform: scale(1);
    box-shadow: 0 0 0 0.8rem rgba(52, 172, 224, 0);
  }
  
  100% {
    transform: scale(0.98);
    box-shadow: 0 0 0 0 rgba(52, 172, 224, 0);
  }
}